import React from 'react';

import './DetailsBiere.scss';


class DetailsBiere extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_biere: 1,
            nom: "ma biere 0",
            brasserie: "ma brasserie",
            image: "",
            //commentaires: [{commentaire: "allo", courriel: "moi@moi.ca"}, {commentaire: "allo", courriel: "moi@moi.ca"}],
            commentaires: [],
            nouveauCommentaire: "",
            note:{}
        };


        console.log("Details bière")
        // this.nouvCommentaire = React.createRef();

        this.soumettreCommentaire = this.soumettreCommentaire.bind(this);
        this.changement = this.changement.bind(this);
        this.getCommentaires = this.getCommentaires.bind(this);
        this.getNote = this.getNote.bind(this);
    }

    changement(element) {
        let { name, value } = element.target;
        this.setState({ [name]: value });

    }
    getNote(id){
        fetch('http://127.0.0.1:8000/webservice/php/biere/' + id + '/note/')
        .then(response => response.json())
        .then(donnees => { console.log(donnees); this.setState({note: donnees.data})})
    }

    getCommentaires(id) {
        fetch('http://127.0.0.1:8000/webservice/php/biere/' + id + '/commentaire/')
            .then(response => response.json())
            .then(donnees => { console.log(donnees); this.setState({ commentaires: donnees.data }) })
    }

    soumettreCommentaire() {
        console.log("envoie commentaires");
        //console.log(this.nouvCommentaire.current);
        //console.log(this.state);
        let data = {
            commentaire: this.state.nouveauCommentaire,
            courriel: this.props.courriel
        };

        const { id } = this.props.match.params;
        this.setState({id_biere : id});

        //let entete = new Headers({'Authorization':'Basic '+btoa("biero" + ":" + "biero")});
        let entete = new Headers();
        entete.set('Authorization', 'Basic ' + btoa("biero:biero"));
        //entete.append('Authorization', 'Basic Ymllcm86Ymllcm8='); //btoa("biero" + ":" + "biero"));

        let init = {
            method: 'PUT',
            headers: entete,
            withCredentials: true,
            mode: 'cors',
            body: JSON.stringify(data)
        };


        fetch('http://127.0.0.1:8000/webservice/php/biere/' + id + '/commentaire/', init)
            .then(response => response.json())
            .then(() => {
                this.getCommentaires(id);
                this.setState({ nouveauCommentaire: "" });

            })
            .catch(function (e) {
                console.log(e);
                console.log("erreur dans la requête");
            });
    }

    faireVote(note){
        
        let entete = new Headers();
        entete.set('Authorization', 'Basic ' + btoa("biero:biero"));
        let data = {
            "courriel": this.props.courriel,
            "note": note+1
          }
        console.log(data)
        let init = {
            method: 'PUT',
            headers: entete,
            withCredentials: true,
            mode: 'cors',
            body: JSON.stringify(data)
        };

        const { id } = this.props.match.params;
        fetch('http://127.0.0.1:8000/webservice/php/biere/' + id + '/note/', init)
            .then(response => response.json())
            .then(() => {
                this.getNote(id);

            })
            .catch(function (e) {
                console.log(e);
                console.log("erreur dans la requête");
            });
    }


    componentDidMount() {
        //console.log(params);
        const { id } = this.props.match.params;

        console.log(id);


        fetch('http://127.0.0.1:8000/webservice/php/biere/' + id)
            .then(response => response.json())
            .then(donnees => this.setState(donnees.data));
        
        this.getCommentaires(id);
        this.getNote(id);
    }


    render() {
        let commentairesNodes = this.state.commentaires.map(function (unCommentaire, i) {
            return (
                <article className="unCommentaire" key={i}>
                    <p>Commentaire : <br/>{unCommentaire.commentaire}</p>
                    <p>Courriel : {unCommentaire.courriel}</p>
                </article>
            );
        });
        let ajouter = <section key={this.props.courriel} className="ajoutCommentaire"></section>;
        let vote =<div className="vote"></div>;
        if (this.props.courriel) {
            ajouter = <section key={this.props.courriel} className="ajoutCommentaire">
                <h1 className="titreCommentaire">Ajouter</h1>
                <p>Commentaire : <textarea name='nouveauCommentaire' onChange={this.changement} value={this.state.nouveauCommentaire} /></p>
                <button onClick={this.soumettreCommentaire} >Envoyer</button>
            </section>;

            let nodeNote = [...Array(5)].map((x, i) =>
                <span key={i} onClick={()=>{this.faireVote(i)}}>★</span>
                );

            vote = <div className="vote">Votre note : 
                {nodeNote}
            </div>;
            
        }

       
        return (
            <div>
                <section ref='biere' className='detailsbiere' >
                    <div className='image'><img alt="" src="http://placehold.it/204x204" /></div>
                    <div className='texte'>
                        <span className='nom'>Nom : {this.state.nom}</span>
                        <span className='brasserie'>Brasserie : {this.state.brasserie}</span>
                        <section className='note'>Note : {this.state.note.note}/5 ({this.state.note.nombre})
                            {vote}
                        </section>

                    </div>
                
                
                </section>
                <section className="commentaires" >
                    {ajouter}
                    {commentairesNodes}
                    

                </section>
            </div>
        );
    }
}

export default DetailsBiere;