import React from 'react';
import './ListeBieres.scss';
import {
    Link
} from "react-router-dom";
import Biere from './Biere';

class ListeBieres extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [{
                id_biere: 1,
                nom: "ma biere 0",
                brasserie: "ma brasserie",
                image: ""
            },
            {
                id_biere: 2,
                nom: "ma biere 1",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 3,
                nom: "ma biere 2",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 4,
                nom: "ma biere 3",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 5,
                nom: "ma biere 4",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 6,
                nom: "ma biere 3",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 7,
                nom: "ma biere 4",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 8,
                nom: "ma biere 3",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 9,
                nom: "ma biere 4",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 10,
                nom: "ma biere 3",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 11,
                nom: "ma biere 4",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 12,
                nom: "ma biere 3",
                brasserie: "ma brasserie",
                image: ""
            }, {
                id_biere: 13,
                nom: "ma biere 4",
                brasserie: "ma brasserie",
                image: ""
            }]
        };
    }

    componentDidMount() {
        fetch('http://127.0.0.1:8000/webservice/php/biere')
            .then(response => response.json())
            .then(donnees => this.setState({ data: donnees.data }));
    }

    render() {
        console.log(this.state)
        let biereNodes = this.state.data.map(function (uneBiere, i) {
            return (
                <Link key={uneBiere.id_biere} to={"/biere/" + uneBiere.id_biere}>
                    <Biere key={uneBiere.id_biere} position={i} nom={uneBiere.nom} brasserie={uneBiere.brasserie} image={uneBiere.image}></Biere>
                </Link>
            );
        });

        return (
            <section className='listeBiere accueil'>
                {biereNodes}
            </section>
        );
    }
}
export default ListeBieres;