import React from 'react';
//import logo from '../../assets/logo.svg';
import './Entete.css';
import {
    NavLink,
    Link
} from "react-router-dom";

class Entete extends React.Component {
    constructor(props) {
        super(props);

    }



    render() {
        return (
            <header className="App-header">
                <nav>
                    <div className="top-nav">
                        <div className="barre">
                            <Link className="logo" to="/">
                                B<span>iero</span>
                            </Link>
                            <span className="flex-spacer"></span>
                            <p className="menu-mobile"></p>
                        </div>
                        <span className="flex-spacer"></span>
                        <ul>
                            <li>
                                <NavLink activeClassName="active" to="/biere">Les bières</NavLink>
                            </li>
                            <li>
                                <input name="courriel" onChange={this.props.surChangement}></input><button>Login</button>
                            </li>
                            <li className="social">
                                <a href="#">
                                    <i className="icon-facebook_circle icon2x"></i>
                                </a>
                                <a href="#">
                                    <i className="icon-twitter_circle icon2x"></i>
                                </a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>
        );
    }
}

export default Entete;