import React from 'react';
import './Biere.scss';


class Biere extends React.Component {
    constructor(props) {
        super(props);
    }


    render() {
        return (
            <article ref='biere' className='unBiere init' style={{ animationDelay: this.props.position * 50 + 'ms' }}>
                <div className='image'><img src="204x204.png" /></div>
                <div className='texte'>
                    <span className='nom'>{this.props.nom}</span>
                    <span className='brasserie'>{this.props.brasserie}</span>
                </div>
            </article>
        );
    }
}

export default Biere;