import React from 'react';
//import logo from '../../assets/logo.svg';
import Entete from '../Entete/Entete';
import Accueil from '../Accueil/Accueil';
import ListeBieres from '../Bieres/ListeBieres';
import DetailsBiere from '../Bieres/DetailsBiere';

import {
	BrowserRouter as Router,
	Switch,
	Route,
	//  Link
} from "react-router-dom";

import './App.css';
class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = { "courriel": "" };
		this.surChangementGestionnaire = this.surChangementGestionnaire.bind(this);
	}
	surChangementGestionnaire(e) {
		let { value } = e.target;
		console.log(value);
		this.setState({ "courriel": value });
	}
	render() {
		return (
			<Router className="App" basename={'/client'}>
				<Entete surChangement={this.surChangementGestionnaire} />
				<Switch>
					<Route exact path="/biere" component="">
						<ListeBieres />
					</Route>

					<Route exact path="/biere/:id" render={(props) => <DetailsBiere {...props} courriel={this.state.courriel} />} />

					<Route exact path="/">
						<Accueil />
					</Route>
					<Route exact path="*">
						<p>Aucune route</p>
					</Route>
				</Switch>
			</Router>
		);
	}
}

export default App;
